<script>
import { onMounted, ref, reactive, toRefs, getCurrentInstance, watch } from "vue";
import addico from '../assets/sections/expand.svg'
import delico from '../assets/sections/contract.svg'
export default {
  name: "Home",
  setup() {
    const state = reactive({
      pageModel: {
        current: 1,
        size: 20,
      },
      connect: {
        isConnect: false,
        address: "",
        balance: "",
      },

      totalSupply: 8888,
      mintTotalSupply: 0,
      currentContract: null,
      mintPrice: 10,
      mintNumber: "",
      showModel:{
        tip1:false,
        tip2:false,
        tip3:false,
        tip4:false,
        tip5:false,
        tip6:false,
        tip7:false,
        tip8:false,
        tip9:false,
        tip10:false,
        tip11:false,
        tip12:false,
        tip13:false,
        tip14:false,
        tip15:false,
        tip16:false,
        tip17:false,
        tip118:false,
        tip119:false,
      },
      imgAdd:addico,
      imgDel:delico,
    });
    const { proxy } = getCurrentInstance();

    state.connect = proxy.$store.state.connect;

    // 地址省略显示
    const omitshow = function (value, start, end) {
      if (value != "" && value != null && typeof value != "undefined") {
        var str =
          value.substring(0, start) +
          "..." +
          value.substring(value.length - end);
        return str;
      }
    };
    const connectWallet = async function () {
      const connectResult = await proxy.$utils.CONNECT.linkWallet();
      state.connect.isConnect = connectResult.state;
      if (connectResult.state) {
        state.connect.address = connectResult.address;
        state.connect.balance = connectResult.balance;
        proxy.$store.commit("changeconnect", {
          isConnect: true,
          address: connectResult.address,
          balance: connectResult.balance,
        });
      } else {
        alert(connectResult.msg);
      }
    };
    const disconnect = function () {
      state.connect = {
        isConnect: false,
        address: "",
        balance: "",
      };
      proxy.$store.commit("changeconnect", {
        isConnect: false,
        address: "",
        balance: "",
      });
    };
    //获取已经铸造数量
    const getTotalSupply = async function () {
      let currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      if (currentContract != null) {
        state.currentContract = currentContract;
        state.mintTotalSupply = await proxy.$utils.CONTRACT.totalSupply();
      }
    };
    const getMint = async function () {
      if (state.mintNumber == "" || state.mintNumber == 0) {
        alert("Please enter the correct casting quantity");
        return false;
      }
      if (state.mintNumber > 200) {
        alert("The maximum casting quantity is 200");
        return false;
      }
      if (state.currentContract == null) {
        state.currentContract = proxy.$utils.CONTRACT.getCurrentContract();
      }
      if (state.currentContract != null) {
        let balance = await proxy.$utils.CONNECT.getbalance(
          state.connect.address
        );
        let amount = state.mintNumber * state.mintPrice;

        if (Number(balance) < amount) {
          alert("Insufficient wallet balance");
          console.log(balance, amount);
          return false;
        }
        proxy.$utils.CONTRACT.publicMint(
          state.mintNumber,
          state.mintPrice,
          function (res) {
            alert("Casting success");
            state.mintNumber = "";
            setTimeout(async function () {
              getTotalSupply();
            }, 1000);
          },
          function (err) {
            alert("Casting failed. Please recast later");
          }
        );
      }
    };
    const faqList = [
      {title: 'How many tronz are available?', content: 'There will be 8,888 tronz available in the inaugural release. We will be launching another collection shortly after to continue building our TRON Metaverse.'},
      {title: 'Are all the tronz unique?', content: 'Yes all tronz are uniquely generated and 1/1. '},
      {title: 'Can I buy more than 1 TRON?', content: 'Yes, Presale is limited to 3 tronz per Transaction/Wallet. Public sale will have a limit of 3 pieces per transaction. The maximum per wallet will be 10 tronz. This will give everyone a fair chance at mining while minimizing bot interference.'},
      {title: 'Can I resell my DegenTronz NFT?', content: 'Yes, you are able to resell on the OpenSea platform at anytime you see fit.'},
    ];
    const faqStatus = ref(-1);
    let galleryList = ref([
      '1.png','2.png','3.png','4.png','5.png','6.png','7.png','8.png','9.png','10.png','11.png'
    ]),
      prev = ref(10),
      next = ref(1),
      active = ref(0);
    const methods = {
      showFaq(index) {
        faqStatus.value = faqStatus.value == index ? -1 : index
      },
      handelNext() {
        document.querySelector('.gallery-li.active').classList.add('active-next');
        document.querySelector('.gallery-li.prev').classList.add('prev-active');
        document.querySelector('.gallery-li.next').classList.add('next-default');
        if(document.querySelector('.gallery-li.prev').previousElementSibling){
          document.querySelector('.gallery-li.prev').previousElementSibling.classList.add('default-prev-active');
        }else{
          document.querySelector('.gallery-li:last-child').classList.add('default-prev-active');
        }
        setTimeout(() => {
          prev.value = prev.value == 0 ? galleryList.value.length - 1 : prev.value - 1;
          next.value = next.value == 0 ? galleryList.value.length - 1 : next.value - 1;
          active.value = active.value == 0 ? galleryList.value.length - 1 : active.value - 1;
          console.log(prev.value)
          console.log(next.value)
          console.log(active.value)
        }, 800);
      },
      handelPrev() {
        document.querySelector('.gallery-li.active').classList.add('active-prev');
        document.querySelector('.gallery-li.next').classList.add('next-active');
        document.querySelector('.gallery-li.prev').classList.add('prev-default');
        if(document.querySelector('.gallery-li.next').nextElementSibling){
          document.querySelector('.gallery-li.next').nextElementSibling.classList.add('default-active');
        }else{
          document.querySelector('.gallery-li:first-child').classList.add('default-active');
        }
        setTimeout(() => {
          prev.value = prev.value == galleryList.value.length - 1 ? 0 : prev.value + 1;
          next.value = next.value == galleryList.value.length - 1 ? 0 : next.value + 1;
          active.value = active.value == galleryList.value.length - 1 ? 0 : active.value + 1;
        }, 800);
      },
    };
    onMounted(() => {
      setTimeout(async function () {
        getTotalSupply();
      }, 1000);
    });
    return {
      ...toRefs(state), // 将 state 返回出去，就可以直接使用 state 里面的属性
      ...methods,
      connectWallet,
      disconnect,
      getTotalSupply,
      omitshow,
      getMint,
      faqList,
      faqStatus,
      galleryList,
      prev,
      next,
      active
    };
  },
};
</script>



